html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.compensate-for-scrollbar {
  padding-right: 0 !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
  cursor: pointer;
  background: none;
  font-weight: inherit;
  font-size: inherit;
}

select {
  background-color: inherit;
}

blockquote {
  margin: 0;
}

button {
  border: none;
  font-family: inherit;
  margin: 0;
  padding: 0;
  font-size: inherit;
}

textarea {
  outline: none;
  border: none;
  resize: none;
}

textarea:focus {
  outline-width: 0;
}

input,
input::placeholder {
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  border: none;
  outline: none;
}

input::placeholder {
  color: inherit;
}

picture {
  display: block;
}
picture img {
  width: 100%;
  max-width: 100%;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  margin-block-end: 0;
  margin-block-start: 0;
}

ul li {
  list-style-type: none;
}

ol {
  margin: 0;
  padding: 0;
}

ol li {
  list-style-position: inside;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

*, *::after, *::before {
  padding: 0;
  margin: 0;
}

.to_animate {
  transition: opacity 0.8s, transform 0.8s;
}

.draw-center {
  transition: mask-size 2s;
}
.draw-center.to_animate {
  mask-image: url("../img/animations/mask.svg");
  mask-repeat: repeat-y;
  mask-position: center;
  mask-size: 0;
}
.draw-center.to_animate.animated {
  mask-size: 100%;
}

.draw-left {
  transition: mask-size 2s;
}
.draw-left.to_animate {
  mask-image: url("../img/animations/mask.svg");
  mask-repeat: repeat-y;
  mask-size: 0;
}
.draw-left.to_animate.animated {
  mask-size: 100%;
}

.down-fade.to_animate {
  opacity: 0;
  transform: translateY(50px);
}
.down-fade.to_animate.animated {
  opacity: 1;
  transform: translateY(0);
}

.up-fade.to_animate {
  opacity: 0;
  transform: translateY(-50px);
}
.up-fade.to_animate.animated {
  opacity: 1;
  transform: translateY(0);
}

.left-fade.to_animate {
  opacity: 0;
  transform: translateX(-50px);
}
.left-fade.to_animate.animated {
  opacity: 1;
  transform: translateX(0);
}

.right-fade.to_animate {
  opacity: 0;
  transform: translateX(50px);
}
.right-fade.to_animate.animated {
  opacity: 1;
  transform: translateX(0);
}

.rotate-left.to_animate {
  transform: rotate3d(0, 1, 0, 90deg);
}
.rotate-left.to_animate.animated {
  transform: none;
}

.rotate-up.to_animate {
  transform: rotate3d(0, 0, 1, 90deg);
}
.rotate-up.to_animate.animated {
  transform: none;
}

.fade-out.to_animate {
  opacity: 0;
  transform: scale(0.5);
}
.fade-out.to_animate.animated {
  opacity: 1;
  transform: none;
}

.fade-in.to_animate {
  opacity: 0;
  transform: scale(1.5);
}
.fade-in.to_animate.animated {
  opacity: 1;
  transform: none;
}

html {
  scrollbar-gutter: stable;
}

body {
  font-family: "Inter";
}
body.lock {
  overflow: hidden;
}

.page {
  overflow: hidden;
  padding-top: 82px;
}
@media (min-width: 1024px) {
