}

.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.hamRotate.active {
  transform: rotate(45deg);
}

.hamRotate180.active {
  transform: rotate(180deg);
}

.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #2B3746;
  stroke-width: 5.5;
  stroke-linecap: round;
}

.ham1 .top {
  stroke-dasharray: 40 139;
}

.ham1 .bottom {
  stroke-dasharray: 40 180;
}

.ham1.active .top {
  stroke-dashoffset: -98px;
}

.ham1.active .bottom {
  stroke-dashoffset: -138px;
}

.primary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 48px;
  background-color: #EBF3FE;
  gap: 15px;
  border-radius: 20px;
  transition: 0.3s;
}
.primary-button__plus {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background-color: #FFFFFF;
  position: relative;
  transition: background-color 0.3s;
}
.primary-button__plus::after {
  content: "";
  display: block;
  width: 12px;
  height: 2px;
  position: absolute;
  top: 15px;
  left: 10px;
  background-color: #82ADFD;
}
.primary-button__plus::before {
  content: "";
  display: block;
  width: 2px;
  height: 12px;
  position: absolute;
  top: 10px;
  left: 15px;
  background-color: #82ADFD;
  transition: transform 0.3s;
}
.primary-button__text {
  font-weight: 600;
  font-size: 16px;
  color: #6398FD;
}
@media (hover: hover) {
  .primary-button:hover {
    background-color: rgba(130, 173, 253, 0.3019607843);
  }
  .primary-button:hover .primary-button__plus::before {
    transform: rotate(90deg);
  }
}
@media (max-width: 1023px) {
  .primary-button {
    padding: 14px;
    border-radius: 10px;
  }
  .primary-button__text {
    font-size: 16px;
  }
}

.nav-buttons {
  display: flex;
  gap: 10px;
}

.nav-button {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: 0.3s;
}
.nav-button svg {
  max-height: 30px;
  filter: brightness(1);
  transition: 0.3s;
}
.nav-button_blue {
  background-color: rgba(208, 224, 254, 0.4);
}
.nav-button_blue.swiper-button-disabled {
  opacity: 0.5;
}
.nav-button_blue path {
  fill: #4A7AD4;
}
@media (hover: hover) {
  .nav-button_blue:not(.swiper-button-disabled):hover {
    background-color: #4A7AD4;
  }
  .nav-button_blue:not(.swiper-button-disabled):hover svg {
    filter: brightness(3);
  }
}
.nav-button_white {
  background-color: #FFFFFF;
}
.nav-button_white path {
  fill: #82ADFD;
}
.nav-button_back {
  transform: rotate(180deg);
}

